// PikachuGame.js
import React, { useEffect } from 'react';

const PikachuGame = ({ width = 432, height = 304 }) => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const { settings, SCALE_MODES, Renderer, BatchRenderer, autoDetectRenderer, Prepare, Container, Loader, SpritesheetLoader, Ticker, CanvasRenderer, CanvasSpriteRenderer, CanvasPrepare } = require('@pixi/settings');
      const { PikachuVolleyball } = require('./pikavolley');
      const { ASSETS_PATH } = require('./assets_path');

      Renderer.registerPlugin('prepare', Prepare);
      Renderer.registerPlugin('batch', BatchRenderer);
      CanvasRenderer.registerPlugin('prepare', CanvasPrepare);
      CanvasRenderer.registerPlugin('sprite', CanvasSpriteRenderer);
      Loader.registerPlugin(SpritesheetLoader);

      settings.RESOLUTION = 2;
      settings.SCALE_MODE = SCALE_MODES.NEAREST;
      settings.ROUND_PIXELS = true;

      const renderer = autoDetectRenderer({
        width,
        height,
        antialias: false,
        backgroundColor: 0x000000,
        backgroundAlpha: 1,
        forceCanvas: true,
      });

      const stage = new Container();
      const ticker = new Ticker();
      const loader = new Loader();

      const gameCanvasContainer = document.getElementById('game-canvas-container');
      if (gameCanvasContainer) {
        gameCanvasContainer.appendChild(renderer.view);
      }
      renderer.render(stage);

      loader.add(ASSETS_PATH.SPRITE_SHEET);
      for (const prop in ASSETS_PATH.SOUNDS) {
        loader.add(ASSETS_PATH.SOUNDS[prop]);
      }

      loader.onComplete.add(() => {
        const pikaVolley = new PikachuVolleyball(stage, loader.resources);
        startGame(pikaVolley);
      });
      loader.load();

      function startGame(pikaVolley) {
        ticker.maxFPS = pikaVolley.normalFPS;
        ticker.add(() => {
          pikaVolley.gameLoop();
          renderer.render(stage);
        });
        ticker.start();
      }
    }
  }, [width, height]);

  return <div id="game-canvas-container" style={{ width, height }} />;
};

export default PikachuGame;
